/* harmony default export */ __webpack_exports__["default"] = ("<nav\r\n  class=\"w-100 navbar navbar-expand-lg navbar-light bg-light position-relative\"\r\n>\r\n  <div class=\"container\">\r\n    <a class=\"navbar-brand\" href=\"#\">\r\n      <img src=\"assets/logo-fish.png\" alt=\"\" />\r\n    </a>\r\n    <button\r\n      class=\"navbar-toggler\"\r\n      type=\"button\"\r\n      data-toggle=\"collapse\"\r\n      data-target=\"#navbarSupportedContent\"\r\n      aria-controls=\"navbarSupportedContent\"\r\n      aria-expanded=\"false\"\r\n      aria-label=\"Toggle navigation\"\r\n    >\r\n      <span class=\"navbar-toggler-icon\"></span>\r\n    </button>\r\n\r\n    <div class=\"collapse navbar-collapse\" id=\"navbarSupportedContent\">\r\n      <ul class=\"navbar-nav mr-auto\">\r\n        <li class=\"nav-item\">\r\n          <a pageScroll href=\"#meeting_section\" class=\"nav-link\">Meetings</a>\r\n        </li>\r\n        <li class=\"nav-item\">\r\n          <a pageScroll href=\"#about_section\" class=\"nav-link\">About us</a>\r\n        </li>\r\n        <li class=\"nav-item\">\r\n          <a pageScroll href=\"#bap_section\" class=\"nav-link\">BAP</a>\r\n        </li>\r\n        <li class=\"nav-item\">\r\n          <a pageScroll href=\"#contact_section\" class=\"nav-link\">Contact us</a>\r\n        </li>\r\n      </ul>\r\n      <form class=\"form-inline my-2 my-lg-0\">\r\n        <input\r\n          class=\"form-control mr-sm-2\"\r\n          type=\"search\"\r\n          placeholder=\"Search\"\r\n          aria-label=\"Search\"\r\n        />\r\n        <button class=\"btn btn-outline-success my-2 my-sm-0\" type=\"submit\">\r\n          Search\r\n        </button>\r\n      </form>\r\n    </div>\r\n  </div>\r\n</nav>\r\n\r\n<div class=\"container\">\r\n  <img\r\n    class=\"position-absolute\"\r\n    width=\"300px\"\r\n    src=\"assets/fish-aquarium-svgrepo-com.svg\"\r\n    alt=\"\"\r\n    style=\"right: 100px;\"\r\n  />\r\n  <section id=\"header_section position-relative\" style=\" height: 55vh; \">\r\n    <div class=\"row\" style=\"margin-top: 100px\">\r\n      <div class=\"card col-md-6 offset-md-1 position-relative p-3\">\r\n        <h1>Montreal Aquarium Society</h1>\r\n        <p>\r\n          Celebrating over 80 years of promoting the aquarium hobby\r\n        </p>\r\n        <hr />\r\n        <h2>Up comming events</h2>\r\n        <div class=\"\">\r\n          <p class=\"mt-3\">\r\n            Our next meeting will be held Wednesday, <br />December 11, 2019.\r\n            <br />\r\n            <a pageScroll href=\"#meeting_section\"\r\n              >See our Meetings page for details of the evening</a\r\n            >\r\n          </p>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </section>\r\n  <hr />\r\n  <section id=\"meeting_section\">\r\n    <h3 class=\"my-5 clear-top\">Our Meetings</h3>\r\n    <div class=\"row\">\r\n      <div class=\"col-md-6\">\r\n        <p>\r\n          This month we will be having our Christmas Get Together A chance to\r\n          meet & discuss the hobby As usual there will be an auction and a\r\n          raffle We invite you to join our Facebook Group Montreal Aquarium\r\n          Society\r\n        </p>\r\n        <p>\r\n          Each meeting consists of an introduction of the night's activities,\r\n          followed by a presentation by a speaker on various subjects related to\r\n          tropical fish keeping. There is a break during which raffle tickets\r\n          are sold. We then draw for monthly door prizes (members in attendance\r\n          only) and the raffle (open to all who purchased tickets). Finally,\r\n          there is a monthly auction during which anyone can buy the items up\r\n          for sale. (Note items in the auction can only come from members)\r\n        </p>\r\n      </div>\r\n      <div class=\"col-md-6 card\">\r\n        <div class=\"card-body\">\r\n          <h3>Next meeting:</h3>\r\n          <p>Wednesday, December 11, 2019</p>\r\n          <p>7:45 PM, with doors opening at 7:25 PM</p>\r\n          <p>\r\n            Location:\r\n            <br />\r\n            CRCS St Zotique 75, <br />Sir Georges Etienne Cartier Sq\r\n            Montr√©al,<br />QC H4C 3A1\r\n\r\n            <a\r\n              href=\"http://maps.google.com/maps?f=q&source=s_q&hl=fr&geocode=&q=75+Sir-G.-%C3%89tienne-Cartier+Square,+Montr%C3%A9al,+QC+H4C+3A1,+Canada&sll=37.0625,-95.677068&sspn=61.065158,135.351563&ie=UTF8&z=17&iwloc=r3\"\r\n              >Map</a\r\n            >\r\n            <br />\r\n            Note: The room number will be posted on the board directly in front\r\n            of the entrance\r\n          </p>\r\n        </div>\r\n        <hr />\r\n        <div class=\"card-body\">\r\n          <h4>Membership Fees</h4>\r\n          <ul>\r\n            <li>\r\n              Individual - $25.00 per year\r\n            </li>\r\n            <li>\r\n              Family - $30.00 per year\r\n            </li>\r\n            <li>\r\n              Visitors - $4.00 per meeting\r\n            </li>\r\n          </ul>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </section>\r\n  <hr />\r\n  <section id=\"about_section\">\r\n    <h2>About us</h2>\r\n    <p>\r\n      You have come to this site because you, just like our members, are\r\n      involved with tropical fish. Let me tell you a little about us. A few\r\n      people got together in 1933 to share their knowledge of keeping fish in\r\n      the home aquarium. This small group met in each others homes and\r\n      eventually had to expand to larger locales. Henceforth, monthly meetings\r\n      were held which are still perpetuated today. <br /><br />\r\n\r\n      We are a non-profit organization and the goals are to promote the mutual\r\n      interest of its members in acquiring, breeding and raising aquarium fish\r\n      and plant life.<br /><br />\r\n\r\n      Our monthly meetings consist of different activities which are outlined\r\n      when browsing this site. I invite you to come to one of our meetings to\r\n      check us out and learn about our club.\r\n    </p>\r\n  </section>\r\n  <hr />\r\n  <section id=\"bap_section\">\r\n    <h2>BAP - The Breeders Award Program</h2>\r\n    <p>The BAP was established in 1972 with four main goals:</p>\r\n    <ul>\r\n      <li>To recognize outstanding achievements in the breeding of fish.</li>\r\n      <li>To encourage the spawning of aquarium fish.</li>\r\n      <li>To share knowledge of breeding techniques.</li>\r\n      <li>To enable more club members to participate in these activities.</li>\r\n    </ul>\r\n\r\n    <h3>\r\n      <a\r\n        class=\"color-primary\"\r\n        data-toggle=\"collapse\"\r\n        href=\"#collapseExample\"\r\n        aria-expanded=\"false\"\r\n        aria-controls=\"collapseExample\"\r\n        style=\"cursor: pointer;\"\r\n      >\r\n        BAP Subscription Form <i class=\"fa fa-mouse-pointer\"></i>\r\n      </a>\r\n    </h3>\r\n\r\n    <div class=\"collapse mx-3 card\" id=\"collapseExample\">\r\n      <form class=\"card-body\">\r\n        <div class=\"form-row\">\r\n          <div class=\"form-group col-md-6\">\r\n            <label for=\"bapName\">Full Name</label>\r\n            <input type=\"text\" class=\"form-control\" id=\"bapName\" />\r\n          </div>\r\n          <div class=\"form-group col-md-6\">\r\n            <label for=\"bapEmail\">Email</label>\r\n            <input type=\"email\" class=\"form-control\" id=\"bapEmail\" />\r\n          </div>\r\n        </div>\r\n        <div class=\"form-group\">\r\n          <label for=\"bapSpeciesSpawned\">Species Spawned</label>\r\n          <input type=\"text\" class=\"form-control\" id=\"bapSpeciesSpawned\" />\r\n        </div>\r\n        <div class=\"form-group\">\r\n          <label for=\"inputAddress2\">Address 2</label>\r\n          <input\r\n            type=\"text\"\r\n            class=\"form-control\"\r\n            id=\"inputAddress2\"\r\n            placeholder=\"Apartment, studio, or floor\"\r\n          />\r\n        </div>\r\n        <div class=\"form-row\">\r\n          <div class=\"form-group col-md-6\">\r\n            <label for=\"inputCity\">City</label>\r\n            <input type=\"text\" class=\"form-control\" id=\"inputCity\" />\r\n          </div>\r\n          <div class=\"form-group col-md-4\">\r\n            <label for=\"inputState\">State</label>\r\n            <select id=\"inputState\" class=\"form-control\">\r\n              <option selected>Choose...</option>\r\n              <option>...</option>\r\n            </select>\r\n          </div>\r\n          <div class=\"form-group col-md-2\">\r\n            <label for=\"inputZip\">Zip</label>\r\n            <input type=\"text\" class=\"form-control\" id=\"inputZip\" />\r\n          </div>\r\n        </div>\r\n        <div class=\"form-group\">\r\n          <div class=\"form-check\">\r\n            <input class=\"form-check-input\" type=\"checkbox\" id=\"gridCheck\" />\r\n            <label class=\"form-check-label\" for=\"gridCheck\">\r\n              Check me out\r\n            </label>\r\n          </div>\r\n        </div>\r\n        <button type=\"submit\" class=\"btn btn-primary\">Sign in</button>\r\n      </form>\r\n    </div>\r\n  </section>\r\n  <hr />\r\n  <section id=\"contact_section\">\r\n    <h2>Contact us</h2>\r\n    <div class=\"row\">\r\n      <div class=\"col-sm-6 container\">\r\n        <p>\r\n          Hi everyone, I'm Jim Cartier, President, <br />\r\n          You can email or call: <br />\r\n          mas092011@yahoo.ca, 514-648-4936\r\n        </p>\r\n        <p>\r\n          Get in touch with us! Please feel free to contact us at any time. We\r\n          are always happy to help you. We take great pride in our club and\r\n          strive to help each other increase our knowledge of the hobby and to\r\n          share the enjoyment that keeping tropical fish brings.\r\n        </p>\r\n      </div>\r\n      <div class=\"col-sm-6\">\r\n        <form>\r\n          <div class=\"form-group\">\r\n            <label for=\"nameInput\">Name</label>\r\n            <input type=\"text\" class=\"form-control\" id=\"nameInput\" />\r\n          </div>\r\n          <div class=\"form-group\">\r\n            <label for=\"emailInput\">Email Address</label>\r\n            <input type=\"email\" class=\"form-control\" id=\"emailInput\" />\r\n          </div>\r\n          <div class=\"form-group\">\r\n            <label for=\"phoneInput\">Phone Number</label>\r\n            <input type=\"phone\" class=\"form-control\" id=\"phoneInput\" />\r\n          </div>\r\n          <div class=\"form-group\">\r\n            <label for=\"messageInput\">Message</label>\r\n            <textarea\r\n              class=\"form-control\"\r\n              id=\"messageInput\"\r\n              rows=\"3\"\r\n            ></textarea>\r\n          </div>\r\n        </form>\r\n      </div>\r\n    </div>\r\n  </section>\r\n  <hr />\r\n  <section>\r\n    <h2>Friends and partners</h2>\r\n  </section>\r\n  <footer>\r\n    <div class=\"row\">\r\n      <div class=\"col-md-6\"></div>\r\n      <div class=\"col-md-6\">\r\n        Useful links:\r\n        <ul>\r\n          <li>\r\n            <a href=\"http://aquasam.qc.ca/\"\r\n              >SAM - Societe D'Aquariophilie de Montreal</a\r\n            >\r\n          </li>\r\n          <a href=\"http://ca-en.hagen.com/Aquatic?hidden=true\">\r\n            <img\r\n              src=\"https://themontrealaquariumsociety.com/sitebuilder/images/Hagen1_copy-170x58.jpg\"\r\n              alt=\"\"\r\n            />\r\n          </a>\r\n        </ul>\r\n      </div>\r\n    </div>\r\n  </footer>\r\n</div>\r\n");

